// src/api/supplier.ts
import request from '../utils/request'

// 获取供应商列表
export function getSuppliers(api: string, page: number, pageSize: number, search: Record<string, any> = {}) {
    return request({
        url: api,
        method: 'get',
        params: {
            page,
            page_size: pageSize,
            search: JSON.stringify(search)
        }
    });
}

// 获取供应商详情
export function getSupplierDetail(api: string, id: number) {
    return request({
        url: `${api}/${id}`,
        method: 'get'
    });
}

// 新增供应商
export function addSupplier(api: string, data: Record<string, any> = {}) {
    return request({
        url: `${api}`,
        method: 'post',
        data
    });
}

// 编辑供应商
export function editSupplier(api: string, data: Record<string, any> = {}) {
    return request({
        url: `${api}`,
        method: 'post',
        data
    });
}

// 删除供应商
export function deleteSupplier(api: string, ids: number | number[]) {
    return request({
        url: `${api}`,
        method: 'post',
        data: { ids: Array.isArray(ids) ? ids : [ids] }
    });
}

// 状态开关
export function toggleSupplierStatus(api: string, id: number) {
    return request({
        url: `${api}`,
        method: 'post',
        data: { id }
    });
}

// 预付检验
export function prepayCheck(api: string, data: Record<string, any> = {}) {
    return request({
        url: `${api}`,
        method: 'post',
        data
    });
}

// 获取启用的Telegram管理员列表
export function getTelegramAdmins(api: string) {
    return request({
        url: `${api}`,
        method: 'get'
    });
}
